import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { isTagShop } from '../../../actions/api';
import { filterPostDataAppendWebFilter } from '../../../actions/filterpostAction'
import "./scss/Minimal.scss"
import AverageRating from "../../Themes/Elements/AverageRating/AverageRating";
import { onSitePopup } from "../../../actions/OtherActions";

class Minimal extends PureComponent {
       render() {
              const { wall } = this.props;
              const { Reviews, SnapUp } = wall
              const { button, title, reviewDetails, star } = Reviews
              return (
                     <div className="tb_m_container">
                            <div className={`tb_m_title tb-cTBfont-${title?.style?.variant}`}>{title.text}</div>
                            <div className="tb_m_wrapper">
                                   <div className="tb_m_rating">
                                          <div className="tb_m_rating_wrap">
                                                 <div className="tb_m_avg">
                                                        <div className="tb_m_rview_star">
                                                               <AverageRating size={20} rating={parseFloat(reviewDetails?.avgRating)} color={star?.style?.color} />
                                                        </div>
                                                        <div className={`tb_m_review_total_post`}>
                                                               {reviewDetails?.avgRating} out of 5
                                                        </div>
                                                 </div>
                                                 <div className="tb_m_total">Based on {reviewDetails?.totalReviewGiven} reviews</div>
                                          </div>
                                   </div>
                                   {SnapUp.Form.status && isTagShop ? <div className="tb_m_banner_btn_wrap" ref={this.dropdownRef}>
                                          <div className={`tb_m_banner_btn tb-cTBfont-${button.style.variant}`} onClick={() => this.props.onSitePopup(true)}>{button.text}</div>
                                   </div> : null}
                            </div>
                     </div>
              );
       }
}

const mapDispatchToProps = dispatch => {
       return {
              filterPostDataAppendWebFilter: (wallID, timeStamp, postCount, networkId, after, heightEvent, tagName) => dispatch(filterPostDataAppendWebFilter(wallID, timeStamp, postCount, networkId, after, heightEvent, tagName)),
              onSitePopup: (status) => dispatch(onSitePopup(status)),
       }
}
export default connect("", mapDispatchToProps)(Minimal);